import React, { Component } from 'react';
import './App.css';

import logoSVG from './assets/logo.svg';
import designSVG from './assets/design.svg';
import musicSVG from './assets/music.svg';

// Design Projects
import sutureSVG from './assets/artwork/design/suture.svg';
import sentrySVG from './assets/artwork/design/sentry.svg';
import twitchSVG from './assets/artwork/design/twitch.svg';
import runnableSVG from './assets/artwork/design/runnable.svg';

// Music Projects
import togetherSVG from './assets/artwork/music/together.svg';
import separatedSVG from './assets/artwork/music/separated.jpg';

const name = 'Rickie Sherman';
const location = 'Oakland, California';
const headerImgURL = '';

const nav = [
  { name: 'design', glyphURL: designSVG },
  { name: 'music', glyphURL: musicSVG }
];

const projects = {
  // Design Projects List
  // you must include http:// or https:// before URLs
  design: [
    {
      artURL: sutureSVG,
      name: 'suture',
      desc: 'Design Studio',
      year: '2018',
      url: 'http://sutu.re'
    },
    {
      artURL: sentrySVG,
      name: 'sentry',
      desc: 'Error Monitoring',
      year: '2018',
      url: 'http://sentry.io'
    },
    {
      artURL: runnableSVG,
      name: 'runnable',
      desc: 'Error Monitoring',
      year: '2017',
      url: 'http://runnable.com'
    },
    {
      artURL: twitchSVG,
      name: 'twitch',
      desc: 'Livestreaming',
      year: '2016',
      url: 'http://twitch.tv'
    }
  ],
  // Music Projects List
  // you must include http:// or https:// before URLs
  music: [
    {
      artURL: togetherSVG,
      name: 'Together',
      desc: 'Fantastic Night Dreams',
      year: '2018',
      url: 'https://soundcloud.com/rickiesherman/sets/together-fantastic-night-dreams'
    },
    {
      artURL: separatedSVG,
      name: 'Separated',
      desc: 'Dramatic Communication',
      year: '2018',
      url: 'https://soundcloud.com/rickiesherman/sets/separated'
    },
  ]
};

const Nav = ({ onChange, currentNav, elements }) => (
  <div className='nav'>
    { elements.map(item => (
      <button
        onClick={ e => { e.preventDefault(); onChange(item.name); } }
      >
        <img src={ item.glyphURL } />
      </button>
    )) }
  </div>
);

const Header = ({ name, location, headerImgURL, onNav, currentNav, elements }) => (
  <header>
    <div className='header-info'>
      <h1>{ name }</h1>
      <h3>{ location }</h3>
    </div>
    <img src={ headerImgURL } />
    <Nav
      onChange={ onNav }
      currentNav={ currentNav }
      elements={ elements }
    />
  </header>
);

const Projects = ({ projects }) => (
  <div className='projects'>
    { projects.map((project, ind) => (
      <div className={ `project${ ind%2===1 ? ' alt' : '' }` }>
        <img src={ project.artURL } />
        <div className='projectsubline'>
          <div className='projectinfo'>
            <div className='projectname'>{ project.name }</div>
            <div className='projectdesc'>{ project.desc }, { project.year }</div>
          </div>
          <a target='blank' className='projectbutton' href={ project.url }>Visit</a>
        </div>
      </div>
    )) }
  </div>
);

class App extends Component {
  constructor() {
    super();

    this.state = {
      currentNav: 'design',
      currentProjects: projects.design
    };
  }

  onNav = newNav => {
    this.setState({
      currentNav: newNav,
      currentProjects: projects[newNav]
    });
  }

  render() {
    const { currentNav, currentProjects } = this.state;
    return (
      <div className='app'>
        <Header
          name={ name }
          location={ location }
          headerImgURL={ logoSVG }
          onNav={ this.onNav }
          currentNav={ currentNav }
          elements={ nav }
        />
        <Projects
          projects={ currentProjects }
        />
        <a href='mailto:rickiesherman@me.com?subject=Hello, Rickie!'>
          <div className='footer'>Questions? Contact me at rickiesherman@me.com</div>
        </a>
      </div>
    );
  }
}

export default App;



